<template>
    <div class="activePublic" :key="$route.path">
      <div class="source">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/my_account' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <router-view/>
<div v-if="bankAfterShow" class="bank-con">
  <el-col :span="8">
    <el-card>
        <div slot="header" class="clearfix">
          <span class="header-title"></span>
          <!-- <el-button style="float: right; padding: 3px 0" type="text"></el-button> -->
        </div>
        <ul class="address-ul">
          <li><span>: </span>{{bankInfoData.isBusiness==1?'':''}}</li>
          <li><span>: </span>{{bankInfoData.bankName}}</li>
          <li><span>: </span>{{bankInfoData.openBank}}</li>
          <li><span>: </span>{{bankInfoData.number}}</li>
          <li><span>: </span>{{bankInfoData.name}}</li>
          <li><span>: </span>{{bankInfoData.phone}}</li>
        </ul>
    </el-card>
  </el-col>
</div>
<div v-if="bankBeforeShow">
      <el-steps :active="step" class="step" align-center >
        <el-step title="" description=""></el-step>
        <el-step title="" description=""></el-step>
        <el-step title="" description=""></el-step>
        <el-step title="" description=""></el-step>
      </el-steps>

      <transition name="fade">
        <div class="step1" v-if="step==1">
              <!-- -start -->
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
                  <el-form-item label=":">
                    <el-radio-group v-model="ruleForm.isBusiness">
                      <el-radio :label="1"></el-radio>
                      <el-radio :label="0"></el-radio>
                    </el-radio-group>
                  </el-form-item>
                  <!-- -start -->
                  <transition name="slide-fade">
                    <div v-show="ruleForm.isBusiness == '1'">
                      <label style="display:inline-block;text-align:right;width:108px;line-height:40px;padding-right:8px;float:left;" for=""><span style="color:red">* </span>:</label>
                      <el-form-item>
                        <v-distpicker :province="province" :city="city" hide-area @province="onChangeProvince" @city="onChangeCity"></v-distpicker>
                      </el-form-item>
                    </div>

                  <!-- <el-form :inline="true" v-show="ruleForm.isBusiness == '1'" :model="subForm" :rules="subRules" ref="subForm"  class="demo-form-inline">
                    <label style="display:inline-block;text-align:right;width:108px;line-height:40px;padding-right:8px;" for=""><span style="color:red">* </span>:</label>
                    <el-form-item>
                      <el-form-item prop="prov">
                          <el-select v-model="subForm.prov" placeholder="" @change="changeProv">
                            <el-option v-for="(prov,index) in subForm.provs" :label="prov.name" :key="index" :value="prov.id"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item prop="city">
                        <el-select v-model="subForm.city" placeholder="" @change="changeCity">
                          <el-option v-for="(city,index) in subForm.citys" :label="city.name" :key="index" :value="city.id"></el-option>
                        </el-select>  
                      </el-form-item>
                      <el-form-item prop="area">
                        <el-select v-model="subForm.area" placeholder="">
                          <el-option v-for="(area,index) in subForm.areas" :label="area.name" :key="index" :value="area.id"></el-option>
                        </el-select>
                      </el-form-item>
                   </el-form-item>
                  </el-form>                -->
                  </transition>
                  <!-- -end -->
                  <el-form-item label=":" prop="bankId">
                    <el-select v-model='ruleForm.bankId'>
                      <el-option value=""></el-option>
                      <el-option 
                      v-for="(bank,key) in bankList"
                      :key=key
                      :value="bank.bankId"
                      :label="bank.bankName"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item label=":" prop="openBank">
                    <el-input type="input" v-model="ruleForm.openBank" style="width:50%;"></el-input>
                    <router-link to="/bank_help" style="color: #e44747;padding-left:20px;"></router-link>
                    <!-- <a href="http://smerchant.51myd.com/inquiry_bank_help" target="_blank" style="color: #e44747;padding-left:20px;"></a> -->
                  </el-form-item>
                  <el-form-item label=":" prop="number">
                    <el-input type="input" v-model="ruleForm.number" style="width:50%;"></el-input>
                  </el-form-item>
                  <el-form-item label=":" prop="name">
                    <el-input type="input" style="width:50%;" v-model="ruleForm.name"></el-input>
                  </el-form-item>
                  <el-form-item label=":" prop="phone">
                    <el-input type="input" style="width:50%;" v-model="ruleForm.phone"></el-input>
                  </el-form-item>
                  <el-form-item label=":" prop="smsCode">
                      <el-input type="input" style="width:50%;" v-model="ruleForm.smsCode"></el-input>
                      <el-button type="button" style="padding-left:20px;" :disabled="codeDisabled" @click="timeFn">{{codeText}}</el-button>
                  </el-form-item>
                </el-form>  
              <!-- -end -->
        </div>
      </transition>
      <transition name="fade" v-if="step==2">
        <p class="text-center"></p>
      </transition>
      <transition name="fade" v-if="step==3">
          <el-form :inline="true" class="verify-form">
            <el-form-item>
              <el-input placeholder="" v-model="money"></el-input>
            </el-form-item>   
            <el-form-item>
              <el-button type="primary" @click="verify"></el-button>
            </el-form-item>  
          </el-form>
      </transition>
      <transition name="fade" v-if="step==4">
        <div>
          <p class="text-center">{{bankResult}}</p>
          <el-button type="primary" 
          v-show="replaceBtn" @click="reBind"
          style="display:block;margin:30px auto;"></el-button>
        </div>
      </transition>
      <div class="but-group" v-show="nextStep">
        <!-- <el-button @click.native.prevent="handlePreStep" v-show="preStep"></el-button> -->
        <el-button 
        @click="handleNextStep('ruleForm','subForm')" 
        type="primary"
        style="display:block;margin:0 auto;"></el-button>
      </div>
</div>
    </div>
</template>

  <script>
  import VDistpicker from 'v-distpicker'
  import {Row,Col,Button,Breadcrumb,BreadcrumbItem,Steps,Step,Form ,FormItem,Input,Select,Option,RadioGroup,Radio,Notification, Card} from 'element-ui'
  const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
  var validMobile=(rule, val,callback)=>{
      if (!val){
          callback(new Error(''))
      }else  if (!reg.test(val)){
        callback(new Error('11'))
      }else {
        callback()
      }
  }
  //
  const regBank = /^([1-9]{1})(\d{14}|\d{15}|\d{18})$/
  var validBank = (rule,val,callback)=>{
      // let val = val.replace(/\s+/g, "")
      if (!val){
          callback(new Error(''))
      }else  if (!regBank.test(val.replace(/\s+/g, ""))){
        callback(new Error(''))
      }else {
        callback()
      }
  }
    export default {
    name:'activePublic',
    components: {
      VDistpicker,
      'el-button':Button,
      'el-breadcrumb':Breadcrumb,
      'el-breadcrumb-item':BreadcrumbItem,
      'el-steps':Steps,
      'el-step':Step,
      'el-row':Row,
      'el-col':Col,
      'el-form':Form,
      'el-form-item':FormItem,
      'el-input':Input,
      'el-select':Select,
      'el-option':Option,
      'el-radio-group':RadioGroup,
      'el-radio':Radio,
      'el-card':Card
    },
    data: function () {
      return {
        bankResult:'',///
        id:'',//id
        replaceBtn:false,//
        bankInfoData:{},//
        bankAfterShow:false,//
        bankBeforeShow:false,//
        money:'',//
        subForm:{
            prov: '',
            provs:[],
            city:'',
            citys:[],
            areas:[],
            area: ''
        },
        subRules:{
          prov:[
            { required: true, message: '', trigger: 'blur' }
          ],
          city:[
            { required: true, message: '', trigger: 'blur' }
          ],
          area:[
            { required: true, message: '', trigger: 'blur' }
          ]          
        },
        codeDisabled:false,//
        countdown:60,//60s
        codeText:'',//
        bankList:[],
        ruleForm: {
          isBusiness:0,//1:0:
          bankId:null,//
          openBank:'',//
          number:'',//
          name:'',//
          phone:'',//
          smsCode:'',//
        },
        province:'',//
        city:'',//
        rules: {
          bankId:[
            { required: true, message: '', trigger: 'blur' }
          ],
          openBank: [
            { required: true, message: '', trigger: 'blur' }
          ],
          number:[
            // { required: true, message: '', trigger: 'blur' }
            {required:true,validator:validBank,trigger:'blur'}
          ],
          name:[
            { required: true, message: '', trigger: 'blur' }
          ],
          phone:[
            {required:true,validator:validMobile,trigger:'blur'}
          ],
          smsCode:[
            { required: true, message: '', trigger: 'blur' }
          ]
        },

        preStep: false,
        nextStep: true,
        step: 1
      }
    },
  created () {
      this.$store.state.adminleftnavnum="/bank_step"; //active
  }, 
  mounted(){
    let _this = this
    this.changeRoute()
    this.$axios.post(this.$$jk.bankNamesList,{})
        .then(function(response) {
          _this.bankList = response.data.data
          _this.bankInfo()
          
        })
        .catch(function(error) {
            alert(error);
        }); 
//
      this.$axios.post(this.$$jk.getAllProvince,{})
          .then(function(response) {
              if(response.data.code == "SUCCESS"){
                _this.subForm.provs = response.data.data;
              }
          })
          .catch(function(error) {
              alert(error);
          });          
  },
  computed:{

  },
    methods: {
      //Id
      bankInfo(){
        let _this = this
        let id = sessionStorage.getItem('merchantId')
        this.$axios.post(this.$$jk.getBankCardsByMerchantId,{merchantId:id})
            .then(function(response) {
                if(response.data.code == "SUCCESS"){
                  if(response.data.hasOwnProperty('data')){

                    let r = response.data.data
                    let status = r.status
                    if(status==0){
                        _this.bankBeforeShow = true
                        _this.step = 1
                        //                   
                        _this.ruleForm.isBusiness = r.isBusiness
                        _this.ruleForm.bankId = r.bankId
                        _this.ruleForm.openBank = r.openBank
                        _this.ruleForm.number = r.number
                        _this.ruleForm.name = r.name
                        _this.ruleForm.phone = r.phone  
                        _this.province = r.province
                        _this.city = r.city  
                        _this.id = r.id                 
                        _this.$router.push('/bank_step');                      
                    }else if(status == 2){
                        console.log(status)
                        _this.bankBeforeShow = true
                        _this.bankAfterShow = false
                        _this.step = 2
                        _this.$router.push('/bank_step/step'+status);
                    }else if(status == 3){
                        _this.bankBeforeShow = true
                        _this.step = 3
                        _this.id = response.data.data.id
                        _this.$router.push('/bank_step/step'+status);
                    }else if(status == 1){//
                            let path = _this.$route.path
                            if(path == '/bank_step/step4'){
                              _this.bankResult = ""
                              _this.bankBeforeShow = true
                              setTimeout(() => {
                                _this.$router.push('/bank_step');
                              }, 3000);                              
                            }else{
                              _this.bankAfterShow = true
                            }
                            _this.bankInfoData = r
                            _this.ruleForm.bankId = r.bankId
                    }else if(status == 4){
                        _this.step = 4
                        _this.$router.push('/bank_step/step'+status);
                        _this.bankBeforeShow = true
                        _this.bankResult = ""
                        _this.replaceBtn = true
                        _this.id = r.id  
                    }
                        if(r.bankId != ""){
                            let obj = {}
                            obj = _this.bankList.find((item) => {
                              return item.bankId === 20
                            })
                            _this.bankInfoData['bankName'] = obj.bankName
                        }                      
                  }else{
                    _this.bankBeforeShow = true
                    _this.bankAfterShow = false
                  }
                }
            })
            .catch(function(error) {
                alert(error);
            }); 
      },
      //
      reBind(){
        let _this = this
        //0
        this.$axios.post(this.$$jk.reBind,{id:this.id})
        .then(function (response) {
            if(response.data.code == 'SUCCESS'){
              _this.step = 1
              _this.$router.push('/bank_step');
              _this.bankInfo()            
            }
        })
        .catch(function (error) {

        });
      },
      onChangeProvince(data) {
        this.province = data.value
      },
      onChangeCity(data) {
        this.city = data.value
      },
          //
    changeProv(value){
			let _this = this;
			this.subForm.city = '';
			this.subForm.area = '';
			this.subForm.citys = [];
			this.subForm.areas = [];
			this.$axios.post(this.$$jk.getCities,{provinceId:value})
			.then(function (response) {
				if(response.data.code == 'SUCCESS'){
					_this.subForm.citys = response.data.data;
				}else{
					_this.$message({type: 'error',message: response.data.remark});
				}
			})
			.catch(function (error) {
				_this.$message({type: 'error',message: error});
			});
    },
    //
		changeCity(value){
			let _this = this;
			this.subForm.area = '';
			this.subForm.areas = [];
			this.$axios.post(this.$$jk.getAreas,{cityId:value})
			.then(function (response) {
				if(response.data.code == 'SUCCESS'){
					_this.subForm.areas = response.data.data;
				}else{
					_this.$message({type: 'error',message: response.data.remark});
				}
			})
			.catch(function (error) {
				_this.$message({type: 'error',message: error});
			});
		},
      //
      timeFn(){
          let _this = this
          const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
          if (!this.ruleForm.phone){
            this.$message({
              message:'',
              type:'warning'
            })
            return false;
          }else  if (!reg.test(this.ruleForm.phone)){
            this.$message({
              message:'11',
              type:'warning'
            })
            return false;
          } 
        if (this.countdown == 0) { 
            this.codeDisabled = false
            this.codeText=""; 
            this.countdown = 60; 
            return;
        } else { 
            this.codeDisabled = true
            this.codeText="(" + this.countdown + ")"; 
            this.countdown--; 
        } 
        setTimeout(function() { 
            _this.timeFn() }
            ,1000) 
      },
    //
    verify(){
      let _this = this
      let value = this.money
      if(!value){
        this.$message({
          message:'',
          type:'warning'
        })
        return false
      }else if(value<0){
        this.$message({
          message:'',
          type:'warning'
        })
        return false
      }else if(value.indexOf(".") !== -1 && value.substring(value.indexOf("."),value.length).length>3){
        this.$message({
          message:'',
          type:'warning'
        })
        return false
      }
      let a = value.substring(1,0)
      if(a == 0){
        if(value.indexOf('.') != 1){
          this.$message({
            message:'',
            type:'warning'
          })
          return false
        }
      }
      //
      this.$axios.post(this.$$jk.checkBankCards,{id:this.id,money:value})
      .then(function(response) {
        if(response.data.code=='SUCCESS'){
          _this.$router.push('/bank_step/step4');
          _this.step = 4
          if(response.data.data == false){
              _this.$message.error('');
              _this.replaceBtn = true
              return false
          }else{
            _this.$message({
              message:'',
              type:'SUCCESS'
            })
          }
        }
      })
      .catch(function(error) {
          alert(error);
      });       
      
    },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      //
      // handlePreStep: function () {
      //       this.$router.go(-1);
      //       this.step--;
      //       this.goStep(this.step);
      //       this.bankInfo()
      // },
      //
      handleNextStep: function (formName,subForm) {
        let _this = this
        let id = sessionStorage.getItem('merchantId')
        if(this.step==1){
            this.$refs[formName].validate((valid) => {
              if (valid) {
                  let trans
                  this.ruleForm['number'] = this.ruleForm.number.replace(/\s+/g, "");
                  if(this.ruleForm.isBusiness == 1){
                    if(this.province == '' || this.province == ''){
                      this.$message({
                        message:'',
                        type:'warning'
                      })
                      return false
                    }else if(this.city == '' || this.city == ''){
                      this.$message({
                        message:'',
                        type:'warning'
                      })
                      return false
                    }
                    trans = Object.assign({merchantId:id},this.ruleForm,{userKey:id},{province:this.province},{city:this.city})
                  }else{
                    trans =  Object.assign({merchantId:id},this.ruleForm,{userKey:id})
                  }
                  let repTrans
                  if(this.id){//
                    repTrans = Object.assign({id:this.id},trans)
                    this.$axios.post(this.$$jk.bankCardsRebind,repTrans)
                    .then(function(response) {
                      if(response.data.code=='SUCCESS'){
                        _this.$router.push('/bank_step/step'+(_this.step+1));
                      }
                    })
                    .catch(function(error) {
                        alert(error);
                    }); 
                  }else{//
                      repTrans = trans
                      this.$axios.post(this.$$jk.bankCardsBind,repTrans)
                      .then(function(response) {
                        if(response.data.code=='SUCCESS'){
                          _this.$router.push('/bank_step/step'+(_this.step+1));
                        }
                      })
                      .catch(function(error) {
                          alert(error);
                      }); 
                  }

              } else {
                console.log('error submit!!');
                return false;
              }
            });
        }else if(this.step==2){
           this.$router.push('/bank_step/step'+(this.step+1));
        }
          
      },
      goStep: function (n) {
        switch (n) {
          case 1 :
            this.preStep = false;this.nextStep = true;
            break;
          case 2 :
            this.preStep = false;this.nextStep = false;
            break;
          case 3 :
            this.preStep = false;this.nextStep = false;
            break;
          case 4 :
            this.preStep = false;this.nextStep = false;
            break;
        }
      },
      //
      changeRoute(){
        let n = this.$route.path
        switch (n) {
          case '/bank_step/step2' :
            this.step = 2
            this.goStep(2)
            break;
          case '/bank_step/step3' :
            this.step = 3
            this.goStep(3)
            break;
          case '/bank_step/step4' :
            this.step = 4
            this.goStep(4)
            break;
        }
      }
    },
    watch:{      //
      '$route':'changeRoute',
      'codeDisabled':function(val,old){
        let _this = this
        let id = sessionStorage.getItem('merchantId')
        if(val == true){
          Notification ({
            title: '',
            message: ''+this.ruleForm.phone.substr(0, 3) + '****' + this.ruleForm.phone.substr(7)+''
          });   
          //
          this.$axios.post(this.$$jk.sendVerificationCode,{mobile:this.ruleForm.phone,userKey:id,key:'bind_bank'})
          .then(function(response) {})
          .catch(function(error) {
              alert(error);
          });       
        }
      }
    }
  }

</script>
<style>
.step{
  margin:30px auto;
}
.but-group{
  background:#fff;
  padding-bottom:30px;
}
.but-group .el-button:first-child{
  margin-left:100px;
}
.verify-form{
  width:302px;
  margin:0 auto;
}
.step1{
  background:#fff;
  padding:20px 0;
}
/* .el-input{
  width:50%;
} */
.bank-con{
  padding:20px;
}
.address-ul>li:not(:last-child){
  padding-bottom: 15px;
}
.address-ul>li>span{
  display:inline-block;
  width:74px;
  text-align:right;
  margin-right:10px;
  color:#818181;
}
</style>



